import React from 'react';

const Post = (props) => {
    console.log(props.post);

    const{id,title,body}=props.post;
    
    return (
        <div 
          style={{border:'2px solid #0cc8f3',padding:'5px',boxShadow:'5px 5px 5px #f2f2f2',marginBottom:'15px'}}>
            <h2>Number Of Posts : {id}</h2>
            <h1>Posts Title :{title} </h1>
            <p>{body}</p>

        </div>
    );
};

export default Post;