import React from 'react';
import { useState } from 'react';
const Concept = () => {
    return (
        <div>
        <Blog></Blog>
        <Mobile></Mobile>
        <Details author="Mim" title ="CSE"></Details>
        <Details author="Rakib" title ="EEE"></Details>
        <Details author="Hasan" title ="ICE"></Details>
        <hr/>
        <Nayoks></Nayoks>
            
        </div>
    );
};


function Blog(){
  
    return(
      <article className="Blog">
         <h1 style={{textAlign:'center', color:'orange'}}>Depertment Of CSe</h1>
         <p>Lorem ipsum, dolor sit amet consectetur adipisicing elit. Nesciunt deleniti ducimus laudantium neque suscipit libero id magnam accusantium tempore unde pariatur fugit voluptas at sunt dolore aliquam quis eveniet, possimus repellat inventore voluptate exercitationem fugiat. Quia quasi quis iusto cum aliquid! Dolor officia illo numquam quia provident. Nisi, officia perferendis?</p>
      
      </article>
    )
  }
  
  function Details(props){
    return(
      <div>
  
      <h4>Author : {props.author}</h4>
      <p>Heading : {props.title}</p>
      
      </div>
    )
  };
  
  function Mobile(){
   const [down,setDown] =useState(100)
  
   const Bdown =()=>{
    let count = down-10;
    if(count<0){
     count =0;
    }
    setDown(count);
  }
    return(
      <div>
         <h1>Battery Charge is : {down}</h1>
         <button onClick={Bdown}>Bettery is down</button>
      </div>
  
    )
  }
  
  //aray pratics in 
  
  function Nayoks(){
    const nayokss = [
   
          {name:'a',age:25,dept:'cse'},
          {name:'b',age:22,dept:'eee'},
          {name:'c',age:28,dept:'ice'},
          {name:'d',age:29,dept:'phy'}
  
    ]
    return(
      <div>
        {
          nayokss.map(nayok=><Hero nay ={nayok} key={nayok.age}></Hero>)
        }
  
      </div>
    )
  }
  
  function Hero(props){
 
    const{name,age,dept} = props.nay;
    return(
      <div>
         <h1>Name : {name}</h1>
         <h1>Age: {age}</h1>
         <h1>Dept : {dept}</h1>
      </div>
    )
  }
  
export default Concept;